.container {
  overflow: hidden;
}
.container img {
  max-width: 100%;
  height: 100%;
}
.container article:first-child {
  border-radius: 1rem 1rem 0 0;
  overflow: hidden;
}
@media (min-width: 40em) {
  .container article:first-child {
    border-radius: 1rem 0 0 1rem;
  }
}
.container article:last-child {
  background-color: var(--white);
  padding: 1.375rem;
  border-radius: 0 0 1rem 1rem;
}
@media (min-width: 40em) {
  .container article:last-child {
    border-radius: 0 1rem 1rem 0;
  }
}
.container h1 {
  color: var(--very-dark-blue);
  margin-bottom: 1rem;
  font-size: 2rem;
  font-weight: 700;
}
.container h2 {
  text-transform: uppercase;
  font-size: 1rem;
  font-weight: 500;
  color: var(--dark-grayish-blue);
  letter-spacing: 0.2rem;
  margin-bottom: 1rem;
}
.container p {
  color: var(--dark-grayish-blue);
  line-height: 1.8;
  margin-bottom: 2rem;
}
.container ul {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.container ul li:first-child {
  font-size: 1.5rem;
  font-weight: 700;
  margin-right: 1.5rem;
}
.container ul li:last-child {
  color: var(--dark-grayish-blue);
}
.container button,
.container li {
  font-family: inherit;
}
.container .btn {
  background-color: var(--dark-cyan);
  color: var(--white);
  font-weight: 700;
  padding: 1rem 2rem;
  font-size: 1rem;
  width: 100%;
  border: none;
  outline: none;
  cursor: pointer;
  transition: 0.3s;
  border-radius: 0.6rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.container .btn:hover {
  background-color: var(--dark-cyan-hover);
}
.container .btn img {
  margin-right: 0.5rem;
}

.price {
  font-family: var(--font-fraunces);
  color: var(--dark-cyan);
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  background: var(--background);
  color: var(--text-color);
  font-family: var(--font-montserrat);
  font-size: 14px;
  margin: 0 1rem;
  min-height: calc(100vh - 0.1px);
  padding: 0;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.attribution {
  margin-top: 2rem;
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: hsl(228, 45%, 44%);
}

:root {
  --dark-cyan: hsl(158, 36%, 37%);
  --dark-cyan-hover: hsl(158, 59%, 16%);
  --cream: hsl(30, 38%, 92%);
  --very-dark-blue: hsl(212, 21%, 14%);
  --dark-grayish-blue: hsl(228, 12%, 48%);
  --white: hsl(0, 0%, 100%);
  --background: var(--cream);
  --text-color: var(----dark-grayish-blue);
}

:root {
  --font-montserrat: "Montserrat", sans-serif;
  --font-fraunces: "Fraunces", serif;
}

body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.container {
  max-width: 600px;
}
@media (min-width: 40em) {
  .container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
  font-family: var(--font-fraunces);
}

p {
  font-size: 0.875rem;
}

ul {
  list-style-type: none;
  padding-left: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}